  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Inter', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-family  : "Montserrat", sans-serif;
  font-weight  : 900;
}

h2 {
  font-family  : "Montserrat", sans-serif;
  font-weight  : 800;
  border-bottom: 5px solid #feb70d;
  /* font-size : 12px; */
}

h3,
h4 {
  font-family  : "Montserrat", sans-serif;
  font-weight  : 700;
  border-bottom: 5px solid #feb70d;
}

/* Desktop */

@media only screen and (min-width: 769px) {
  #root {
    display: grid;
    grid-template-areas:
      "h a"
      "s m"
      "f f";
    grid-template-rows: 5rem auto 40px;
    grid-template-columns: 4fr 6fr;
    height: 100vh;
    grid-gap: 0.75rem;
  }
}

/* Mobile */

@media only screen and (max-width: 768px) {
  #root {
    display: grid;
    width: 100vw;
    grid-template-areas:
      "h"
      "a"
      "s"
      "m"
      "f";
    grid-template-rows: 70px auto auto 50vh 40px;
    grid-gap: 0.25rem;
  }

  nav {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
}

header {
  grid-area: h;
}

nav {
  grid-area: a;
}

main {
  grid-area: m;
  overflow-y: auto;
}

sidebar {
  grid-area: s;
  overflow-y: auto;
}

@media only screen and (min-width: 769px) {
  sidebar {
    margin-left: 0.5rem;
  }

  main {
    margin-right: 0.5rem;
  }
}

footer {
  grid-area: f;
  background: #ddd;
}

/* desktop styling */

@media only screen and (min-width: 769px) {
  .explorer-map {
    height: calc(100vh - 60px - 11rem);
  }

  .mailer-map {
    height: calc(80vh - 100px - 350px);
  }

  .assignment-map {
    height: calc(100vh - 250px);
  }

  .issue-reporter-map, .linker-map {
    height: calc(100vh - 100px - 1.5rem);
  }
}

/* mobile styling */

@media only screen and (max-width: 768px) {
  .explorer-map {
    height: calc(50vh - 60px);
  }

  .mailer-map {
    height: calc(80vh - 100px - 350px);
  }

  .assignment-map {
    height: calc(50vh - 250px);
  }

  .issue-reporter-map, .linker-map {
    height: calc(50vh - 100px);
  }
}

input[type="radio"] + label span {
